// config/db.js (versión corregida)
export const db = (env) => ({
  prepare: (query) => {
    const stmt = env.DB.prepare(query); // Prepara la consulta directamente
    return {
      bind: (...params) => stmt.bind(...params), // Expone .bind()
      all: async () => await stmt.all(),
      run: async () => {
        const result = await stmt.run();
        return { 
          success: result.success, 
          changes: result.meta.changes 
        };
      }
    };
  }
});