import { Context } from "hono";
import { db } from "../config/db.js";

interface Address {
  address_line: string;
  city: string;
  state: string;
  pincode: string;
  country: string;
  mobile: string;
  user_id: number;
}

interface AddressResponse {
  success: boolean;
  data?: Address | Address[];
  message?: string;
  error?: string;
}

// 🏠 Crear nueva dirección
export const createAddress = async (c: Context) => {
    try {
      // 1. Parsear y validar el cuerpo de la solicitud
      const body = await c.req.json();
      const user = c.get('user');
  
      console.log('[DEBUG] Datos recibidos:', JSON.stringify(body, null, 2));
      console.log('[DEBUG] Usuario:', user?.id);
  
      // 2. Validación mejorada con mensajes detallados
      const requiredFields = [
        { key: 'address_line', name: 'Dirección' },
        { key: 'city', name: 'Ciudad' },
        { key: 'pincode', name: 'Código Postal' },
        { key: 'country', name: 'País' },
        { key: 'mobile', name: 'Teléfono' }
      ];
  
      const missingFields = requiredFields
        .filter(({ key }) => !body[key])
        .map(({ name }) => name);
  
      if (missingFields.length > 0) {
        return c.json(
          {
            success: false,
            error: `Campos requeridos faltantes: ${missingFields.join(', ')}`
          },
          400
        );
      }
  
      // 3. Validación adicional de formato
      if (!/^\d{5}$/.test(body.pincode)) {
        return c.json(
          { success: false, error: 'Código postal inválido (deben ser 5 dígitos)' },
          422
        );
      }
  
      if (!/^\d{10}$/.test(body.mobile)) {
        return c.json(
          { success: false, error: 'Teléfono inválido (deben ser 10 dígitos)' },
          422
        );
      }
  
      // 4. Ejecutar la inserción en la base de datos
      const { meta } = await db(c.env)
        .prepare(
          `INSERT INTO addresses (
            address_line, city, state, pincode, country, mobile, user_id
          ) VALUES (?1, ?2, ?3, ?4, ?5, ?6, ?7)`
        )
        .bind(
          body.address_line,
          body.city,
          body.state || '', // Usar string vacío en lugar de null
          body.pincode,
          body.country,
          body.mobile,
          user.id
        )
        .run();
  
      console.log('[DB] Inserción exitosa. ID:', meta.last_row_id);
  
      // 5. Respuesta exitosa
      return c.json(
        {
          success: true,
          id: meta.last_row_id,
          message: 'Dirección creada exitosamente'
        },
        201
      );
  
    } catch (error) {
      // 6. Manejo de errores mejorado
      console.error('[ERROR] Detalles:', {
        message: error.message,
        stack: error.stack,
        body: await c.req.text().catch(() => 'No se pudo obtener el cuerpo')
      });
  
      return c.json(
        {
          success: false,
          error: 'Error interno al procesar la solicitud'
        },
        500
      );
    }
  };



// 🗺️ Obtener todas las direcciones DEL USUARIO ACTUAL
export const getAddresses = async (c: Context): Promise<Response> => {
    try {
      console.log("🔹 Iniciando getAddresses...");
  
      // 1. Obtener usuario autenticado desde el contexto
      const user = c.get("user");
      if (!user || !user.id) {
        console.warn("⚠️ Usuario no autenticado");
        return c.json(
          { success: false, error: "No autenticado" },
          401
        );
      }
      console.log(`✅ Usuario autenticado: ${user.id}`);
  
      // 2. Consulta SQL con parámetros protegidos
      const query = `
        SELECT 
          id,
          address_line AS addressLine,
          city,
          state,
          pincode,
          country,
          mobile,
          created_at AS createdAt,
          updated_at AS updatedAt
        FROM addresses
        WHERE 
          user_id = ? AND 
          status = 1
        ORDER BY created_at DESC
      `;
  
      console.log("📡 Ejecutando consulta SQL...");
      
      // 3. Ejecutar consulta en D1
      const stmt = c.env.DB.prepare(query).bind(user.id);
      const { results } = await stmt.all();
  
      console.log(`✅ Direcciones obtenidas: ${results.length}`);
  
      // 4. Devolver datos
      return c.json({
        success: true,
        data: results || [],
      });
  
    } catch (error) {
      console.error("❌ Error obteniendo direcciones:", error);
      return c.json(
        { success: false, error: "Error interno del servidor" },
        500
      );
    }
  };
  

// ✏️ Actualizar dirección
export const updateAddress = async (c: Context): Promise<Response> => {
    try {
      console.log("🔹 Iniciando updateAddress...");
  
      const addressId = c.req.param("id");
      const user = c.get("user");
      
      if (!user || !user.id) {
        console.warn("⚠️ Usuario no autenticado");
        return c.json({ success: false, error: "No autenticado" }, 401);
      }
  
      if (isNaN(Number(addressId))) {
        return c.json({ success: false, error: "ID de dirección inválido" }, 400);
      }
  
      const body = await c.req.json<Partial<Address>>();
      console.log("📡 Datos recibidos:", body);
  
      // Ejecutar la actualización
      const { meta } = await c.env.DB.prepare(`
        UPDATE addresses SET
          address_line = COALESCE(?, address_line),
          city = COALESCE(?, city),
          state = COALESCE(?, state),
          pincode = COALESCE(?, pincode),
          country = COALESCE(?, country),
          mobile = COALESCE(?, mobile)
        WHERE id = ? AND user_id = ?
      `)
        .bind(
          body.address_line,
          body.city,
          body.state,
          body.pincode,
          body.country,
          body.mobile,
          addressId,
          user.id
        )
        .run();
  
      if (meta.changes === 0) {
        return c.json({ success: false, error: "Dirección no encontrada o sin cambios" }, 404);
      }
  
      return c.json({ success: true, message: "Dirección actualizada exitosamente" });
  
    } catch (error) {
      console.error("🚨 Error en updateAddress:", error);
      return c.json({ success: false, error: "Error interno del servidor" }, 500);
    }
  };

// 🗑️ Eliminar dirección (soft delete)
export const deleteAddress = async (c: Context): Promise<Response> => {
  try {
    const addressId = c.req.param('id');
    const userId = c.get('user').id;

    // Validar ID numérico
    if (isNaN(Number(addressId))) {
      return c.json({ success: false, error: "ID inválido" }, 400);
    }

    const { meta } = await db(c.env)
      .prepare(`
        UPDATE addresses 
        SET status = 0 
        WHERE id = ? AND user_id = ?
      `)
      .bind(addressId, userId)
      .run();

    if (meta.changes === 0) {
      return c.json(
        { success: false, error: "Dirección no encontrada" },
        404
      );
    }

    return c.json({
      success: true,
      message: 'Dirección eliminada exitosamente'
    });

  } catch (error) {
    console.error("🚨 Error al eliminar dirección:", error);
    return c.json(
      { success: false, error: "Error interno del servidor" },
      500
    );
  }
};