import { Context, Next } from 'hono';
import { jwtVerify } from 'jose';

export const authenticate = async (c: Context, next: Next) => {
  const authHeader = c.req.header('Authorization');
  console.log('🔍 Revisando encabezados:', authHeader);

  if (!authHeader?.startsWith('Bearer ')) {
    console.log('⛔ No se encontró el token en la solicitud');
    return c.json({ error: 'Acceso no autorizado' }, 401);
  }

  const token = authHeader.split(' ')[1];

  try {
    const secret = new TextEncoder().encode(c.env.JWT_SECRET);
    const { payload } = await jwtVerify(token, secret);
    
    console.log('✅ Token decodificado:', payload);

    if (!payload.id || !payload.email) {
      console.log('⛔ El token no contiene `id` o `email`');
      return c.json({ error: 'Token inválido' }, 403);
    }

    // Establecer usuario en el contexto
    c.set('user', {
      id: payload.id,
      email: payload.email
    });

    await next();
  } catch (error) {
    console.error('🚨 Error al verificar el token:', error);
    return c.json({ error: 'Token inválido o expirado' }, 403);
  }
};


export const isAdmin = async (c: Context, next: Next) => {
  const user = c.get('user');

  console.log('🔍 Verificando si el usuario es admin:', user);

  if (!user || !user.id) {
    console.log('⛔ No se pudo obtener el ID del usuario');
    return c.json({ error: 'Acceso no autorizado' }, 403);
  }

  // Buscar el usuario en la base de datos para verificar su rol
  try {
    const query = await c.env.DB.prepare("SELECT role FROM users WHERE id = ?").bind(user.id).first();
    
    if (!query || query.role !== "ADMIN") {
      console.log('⛔ Acceso denegado: No es administrador');
      return c.json({ 
        error: 'Se requieren privilegios de administrador',
        code: 'ADMIN_REQUIRED'
      }, 403);
    }

    console.log('✅ Acceso concedido al admin:', user.id);
    await next();
  } catch (error) {
    console.error('🚨 Error al verificar el rol de usuario:', error);
    return c.json({ error: 'Error interno del servidor' }, 500);
  }
};
