import { Context } from 'hono';
import { HTTPException } from 'hono/http-exception';
import { v4 as uuidv4 } from 'uuid'; // For generating UUIDs

interface OrderProduct {
  product_id: number;
  quantity: number;
  unit_price: number;
  discount?: number;
}

interface CreateOrderBody {
  userId: number; // Asegúrate de que este campo esté presente
  delivery_address: number;
  items: OrderProduct[];
  paymentMethod: string; // Cambiado de `metodo_de_pago` a `paymentMethod`
}




export const createOrder = async (c) => {
  try {
    const user = c.get('user');
    if (!user) throw new HTTPException(401, { message: 'Unauthorized' });

    const body = await c.req.json();
    console.log("Datos recibidos:", JSON.stringify(body, null, 2));

    // Validación de campos requeridos
    if (!body.userId || !body.delivery_address || !body.items?.length) {
      throw new HTTPException(400, { message: 'Campos requeridos faltantes' });
    }

    // Validar que la dirección pertenece al usuario
    const address = await c.env.DB.prepare(`
      SELECT * FROM addresses 
      WHERE id = ? AND user_id = ?
    `).bind(body.delivery_address, user.id).first();

    if (!address) {
      throw new HTTPException(400, { 
        message: 'Dirección no válida o no pertenece al usuario' 
      });
    }

    // Calcular totales
    let subTotal = 0;
    const products = await Promise.all(
      body.items.map(async (item) => {
        // Validaciones de producto
        const product = await c.env.DB.prepare(`
          SELECT id, price, stock FROM products WHERE id = ?
        `).bind(item.product_id).first();

        if (!product) throw new HTTPException(404, { message: 'Producto no encontrado' });
        if (product.stock < item.quantity) throw new HTTPException(400, { message: 'Stock insuficiente' });

        const price = parseFloat(item.unit_price);
        const discount = Math.min(Math.max(parseFloat(item.discount) || 0, 0), 100);
        const total = price * item.quantity * (1 - discount / 100);
        
        subTotal += total;

        return {
          product_id: item.product_id,
          quantity: item.quantity,
          unit_price: price,
          discount: discount,
          total_price: total
        };
      })
    );

    // Crear ID único para la orden
    const order_id = uuidv4();

    // Insertar orden principal
    const order = await c.env.DB.prepare(`
      INSERT INTO orders (
        order_id, 
        user_id,
        delivery_address,
        sub_total_amt,
        total_amt,
        metodo_de_pago,
        payment_status,
        status
      ) VALUES (?, ?, ?, ?, ?, ?, ?, ?)
      RETURNING *
    `).bind(
      order_id,
      user.id,
      body.delivery_address, // Address ID correctamente insertado
      subTotal,
      subTotal,
      body.paymentMethod,
      'PENDING',
      'PENDING'
    ).first();

    // Insertar productos de la orden
    for (const product of products) {
      await c.env.DB.prepare(`
        INSERT INTO order_products (
          order_id, 
          product_id,
          quantity,
          unit_price,
          discount,
          total_price
        ) VALUES (?, ?, ?, ?, ?, ?)
      `).bind(
        order_id,
        product.product_id,
        product.quantity,
        product.unit_price,
        product.discount,
        product.total_price
      ).run();
    }

    // Actualizar stock
    for (const item of body.items) {
      await c.env.DB.prepare(`
        UPDATE products 
        SET stock = stock - ? 
        WHERE id = ?
      `).bind(item.quantity, item.product_id).run();
    }

    return c.json({ 
      success: true, 
      data: {
        ...order,
        address_details: address
      }
    });

  } catch (error) {
    console.error("Error completo:", error);
    if (error instanceof HTTPException) throw error;
    throw new HTTPException(500, { message: 'Error interno del servidor' });
  }
};

