// Helper: Obtener token de acceso PayPal
async function getPayPalAccessToken() {
  const auth = btoa(`${PAYPAL_CLIENT_ID}:${PAYPAL_SECRET}`);
  
  const response = await fetch('https://api-m.sandbox.paypal.com/v1/oauth2/token', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      'Authorization': `Basic ${auth}`
    },
    body: 'grant_type=client_credentials'
  });

  const data = await response.json();
  return data.access_token;
}

// Crear orden PayPal integrado con DB
export const createPayPalOrder = async (c) => {
  try {
    const user = c.get('user');
    if (!user) throw new HTTPException(401, { message: 'Unauthorized' });

    const body = await c.req.json();
    console.log("Datos recibidos:", JSON.stringify(body, null, 2));

    // Validación de campos requeridos
    if (!body.amount || !body.delivery_address || !body.items?.length) {
      throw new HTTPException(400, { message: 'Campos requeridos faltantes' });
    }

    // Validar dirección de entrega
    const address = await c.env.DB.prepare(`
      SELECT * FROM addresses 
      WHERE id = ? AND user_id = ?
    `).bind(body.delivery_address, user.id).first();

    if (!address) {
      throw new HTTPException(400, { 
        message: 'Dirección no válida o no pertenece al usuario' 
      });
    }

    // Validar productos y calcular montos
    let subTotal = 0;
    const products = await Promise.all(
      body.items.map(async (item) => {
        if (!item.product_id) throw new HTTPException(400, { message: 'product_id es requerido' });
        
        const product = await c.env.DB.prepare(`
          SELECT id, price, stock FROM products WHERE id = ?
        `).bind(item.product_id).first();

        if (!product) throw new HTTPException(404, { message: 'Producto no encontrado' });
        if (product.stock < item.quantity) throw new HTTPException(400, { message: 'Stock insuficiente' });

        const price = parseFloat(item.unit_price);
        const discount = Math.min(Math.max(parseFloat(item.discount) || 0, 0), 100);
        const total = price * item.quantity * (1 - discount / 100);
        
        subTotal += total;

        return {
          product_id: item.product_id,
          quantity: item.quantity,
          unit_price: price,
          discount: discount,
          total_price: total
        };
      })
    );

    // Validar coincidencia de montos
    if (Math.abs(subTotal - parseFloat(body.amount)) > 0.01) {
      throw new HTTPException(400, { message: 'El monto total no coincide con los items' });
    }

    // Crear orden en PayPal
    const accessToken = await getPayPalAccessToken();
    const paypalOrderId = uuidv4();
    
    const paypalResponse = await fetch('https://api-m.sandbox.paypal.com/v2/checkout/orders', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${accessToken}`,
        'PayPal-Request-Id': paypalOrderId
      },
      body: JSON.stringify({
        intent: 'CAPTURE',
        purchase_units: [{
          reference_id: paypalOrderId,
          amount: {
            currency_code: 'USD',
            value: subTotal.toFixed(2), // Monto total
            breakdown: {
              item_total: {
                currency_code: 'USD',
                value: subTotal.toFixed(2) // Igual al monto total
              }
            }
          },
          items: body.items.map(item => ({
            name: item.name.substring(0, 127),
            unit_amount: {
              currency_code: 'USD',
              value: parseFloat(item.unit_price).toFixed(2) // Precio unitario
            },
            quantity: item.quantity,
            sku: item.product_id.toString()
          }))
        }]
      })
    });

    const paypalData = await paypalResponse.json();
    if (!paypalResponse.ok) throw new HTTPException(502, { 
      message: `Error PayPal: ${paypalData?.details?.[0]?.description || 'Unknown error'}`
    });

    // Crear orden en DB
    const order = await c.env.DB.prepare(`
      INSERT INTO orders (
        order_id, 
        user_id,
        delivery_address,
        sub_total_amt,
        total_amt,
        metodo_de_pago,
        payment_id,
        payment_status,
        status
      ) VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?)
      RETURNING *
    `).bind(
      paypalOrderId,
      user.id,
      body.delivery_address,
      subTotal,
      subTotal,
      'paypal',
      paypalData.id,
      'PENDING',
      'PENDING'
    ).first();

    // Insertar productos y actualizar stock
    for (const product of products) {
      await c.env.DB.prepare(`
        INSERT INTO order_products (
          order_id,
          product_id,
          quantity,
          unit_price,
          discount,
          total_price
        ) VALUES (?, ?, ?, ?, ?, ?)
      `).bind(
        paypalOrderId,
        product.product_id,
        product.quantity,
        product.unit_price,
        product.discount,
        product.total_price
      ).run();

      await c.env.DB.prepare(`
        UPDATE products 
        SET stock = stock - ? 
        WHERE id = ?
      `).bind(product.quantity, product.product_id).run();
    }

    return c.json({ 
      success: true,
      data: {
        ...order,
        paypal_approval_url: paypalData.links.find(l => l.rel === 'approve')?.href
      }
    });

  } catch (error) {
    console.error("Error completo:", error);
    if (error instanceof HTTPException) throw error;
    throw new HTTPException(500, { message: 'Error interno del servidor' });
  }
};

