// Ejemplo de endpoint para completar orden
export const completeOrder = async (c) => {
  try {
    const { paypalOrderId } = await c.req.json();
    
    // 1. Verificar pago con PayPal
    const captureResponse = await fetch(
      `https://api-m.sandbox.paypal.com/v2/checkout/orders/${paypalOrderId}/capture`,
      {
        method: "POST",
        headers: { "Authorization": `Bearer ${await getPayPalAccessToken()}` }
      }
    );

    const paypalData = await captureResponse.json();
    if (paypalData.status !== 'COMPLETED') {
      throw new HTTPException(402, { message: 'Pago no completado' });
    }

    // 2. Actualizar base de datos
    await c.env.DB.prepare(`
      UPDATE orders 
      SET payment_status = 'COMPLETED', 
          status = 'COMPLETED'
      WHERE payment_id = ?
    `).bind(paypalOrderId).run();

    return c.json({ 
      success: true,
      data: {
        orderId: paypalOrderId,
        captureId: paypalData.id
      }
    });

  } catch (error) {
    console.error("Error completando orden:", error);
    throw new HTTPException(500, { message: "Error al completar orden: " + error.message });
  }
};




export const getOrders = async (c: Context) => {
  try {
    const user = c.get('user');
    if (!user) throw new HTTPException(401, { message: 'Unauthorized' });

    // Obtener las órdenes con los datos del usuario, productos y dirección
    const ordersWithProducts = await c.env.DB.prepare(
      `SELECT 
        o.id AS order_id,
        o.user_id,
        u.name AS user_name,
        u.email AS user_email,
        o.status,
        o.total_amt,
        o.created_at,
        op.product_id,
        p.name AS product_name,
        p.image AS product_image,
        op.unit_price,
        op.discount, 
        op.quantity,
        op.total_price,
        a.id AS address_id, 
        a.address_line,
        a.city,
        a.state,
        a.pincode,
        a.country,
        a.mobile
      FROM 
        orders o
      LEFT JOIN users u ON o.user_id = u.id
      LEFT JOIN order_products op ON o.order_id = op.order_id
      LEFT JOIN products p ON op.product_id = p.id
      LEFT JOIN addresses a ON o.delivery_address = a.id -- 🔹 Se une con la tabla addresses
      ORDER BY o.created_at DESC`
    ).all();

    console.log("Datos brutos de la base de datos:", ordersWithProducts.results);

    // Estructurar los datos para agrupar productos y dirección por orden
    const orders = ordersWithProducts.results.reduce((acc, row) => {
      let order = acc.find((o) => o.order_id === row.order_id);
      if (order) {
        if (row.product_id) {
          order.items.push({
            product_id: row.product_id,
            name: row.product_name,
            image: row.product_image,
            unit_price: row.unit_price,
            discount: row.discount, 
            quantity: row.quantity,
            total_price: row.total_price,
          });
        }
      } else {
        acc.push({
          order_id: row.order_id,
          user_id: row.user_id,
          user_name: row.user_name,
          user_email: row.user_email,
          status: row.status,
          total_amt: row.total_amt,
          created_at: row.created_at,
          address: row.address_id ? { // 🔹 Agregar la dirección solo si existe
            id: row.address_id,
            address_line: row.address_line,
            city: row.city,
            state: row.state,
            pincode: row.pincode,
            country: row.country,
            mobile: row.mobile
          } : null,
          items: row.product_id ? [{
            product_id: row.product_id,
            name: row.product_name,
            image: row.product_image,
            unit_price: row.unit_price,
            discount: row.discount, 
            quantity: row.quantity,
            total_price: row.total_price,
          }] : [],
        });
      }
      return acc;
    }, []);

    console.log("Datos estructurados para el frontend:", orders);

    return c.json({ success: true, data: orders });
  } catch (error) {
    console.error('Error getting orders:', error);
    throw new HTTPException(500, { message: 'Internal server error' });
  }
};



export const updateOrderStatus = async (c: Context) => {
  try {
    const user = c.get('user');
    if (!user) throw new HTTPException(401, { message: 'Unauthorized' });

    const orderId = c.req.param('id');
    const { status } = await c.req.json();

    // Validar que el estado sea válido
    const validStatuses = ['pending', 'completed', 'delivered', 'cancelled', 'shipped'];
    if (!validStatuses.includes(status)) {
      throw new HTTPException(400, { message: 'Invalid status' });
    }

    const order = await c.env.DB.prepare(
      `UPDATE orders SET status = ? WHERE id = ? AND user_id = ? RETURNING *`
    ).bind(status, orderId, user.id).first();

    if (!order) throw new HTTPException(404, { message: 'Order not found' });

    return c.json({ success: true, data: order });
  } catch (error) {
    console.error('Error updating order:', error);
    throw new HTTPException(500, { message: 'Internal server error' });
  }
};


export const getOrderById = async (c: Context) => {
  try {
    const user = c.get('user');
    if (!user) throw new HTTPException(401, { message: 'Unauthorized' });

    const orderId = c.req.param('order_id'); // ID INTEGER de la tabla `orders`
    console.log("Order ID from request:", orderId);
    console.log("User ID from request:", user.id);

    // Obtener la orden desde `orders`
    const orderResult = await c.env.DB.prepare(
      `SELECT * FROM orders WHERE id = ? AND user_id = ?`
    ).bind(orderId, user.id).first();

    if (!orderResult) {
      throw new HTTPException(404, { message: `Orden no encontrada para id: ${orderId}` });
    }

    const { id, delivery_address } = orderResult; // ID de la orden y dirección

    // Obtener los productos de la orden
    const productsResult = await c.env.DB.prepare(
      `SELECT 
        op.product_id,
        p.name AS product_name,
        p.image AS product_image,
        op.unit_price,
        op.discount,
        op.quantity,
        op.total_price
      FROM 
        order_products op
      LEFT JOIN 
        products p ON op.product_id = p.id
      WHERE 
        op.order_id = ?`
    ).bind(id).all();

    console.log("Datos brutos de la base de datos:", productsResult.results);

    // Obtener la dirección de entrega
    let addressResult = null;
    if (delivery_address) {
      addressResult = await c.env.DB.prepare(
        `SELECT id, address_line, city, state, pincode, country, mobile
         FROM addresses 
         WHERE id = ?`
      ).bind(delivery_address).first();
    }

    const order = {
      ...orderResult,
      items: productsResult.results.map((row) => ({
        product_id: row.product_id,
        name: row.product_name,
        image: JSON.parse(row.product_image), // Convertir a array
        unit_price: row.unit_price,
        discount: row.discount ?? 0,
        quantity: row.quantity,
        total_price: row.total_price,
      })),
      address: addressResult || null, // Agregar dirección si existe
    };

    return c.json({ success: true, data: order });
  } catch (error) {
    console.error('Error getting order by ID:', error);
    if (error instanceof HTTPException) throw error;
    throw new HTTPException(500, { message: 'Internal server error' });
  }
};





// delete oreden
export const deleteOrder = async (c: Context) => {
  try {
    const user = c.get('user');
    if (!user) throw new HTTPException(401, { message: 'Unauthorized' });

    const orderId = c.req.param('order_id');

    // Verificar si la orden existe
    const orderExists = await c.env.DB.prepare(
      `SELECT id FROM orders WHERE id = ?`
    ).bind(orderId).first();

    if (!orderExists) {
      throw new HTTPException(404, { message: `Orden no encontrada para id: ${orderId}` });
    }

    // Eliminar productos asociados en `order_products`
    await c.env.DB.prepare(
      `DELETE FROM order_products WHERE order_id = ?`
    ).bind(orderId).run();

    // Eliminar la orden
    await c.env.DB.prepare(
      `DELETE FROM orders WHERE id = ?`
    ).bind(orderId).run();

    return c.json({ success: true, message: 'Orden eliminada correctamente' });
  } catch (error) {
    console.error('Error eliminando orden:', error);
    if (error instanceof HTTPException) throw error;
    throw new HTTPException(500, { message: 'Internal server error' });
  }
};

// update oreden 



export const updateOrder = async (c: Context) => {
  try {
    // Autenticación
    const user = c.get('user');
    if (!user) throw new HTTPException(401, { message: 'No autorizado' });

    // Validación de datos
    const orderId = c.req.param('order_id');
    const body = await c.req.json();
    
    if (!body.status || !body.total_amt) {
      throw new HTTPException(400, { message: 'Datos incompletos' });
    }

    // Validar tipos de datos
    if (typeof body.total_amt !== 'number' || body.total_amt < 0) {
      throw new HTTPException(400, { message: 'Total inválido' });
    }

    // Actualizar en DB
    const result = await c.env.DB.prepare(`
      UPDATE orders 
      SET status = ?, total_amt = ?, updated_at = CURRENT_TIMESTAMP 
      WHERE id = ?
    `).bind(body.status, body.total_amt, orderId).run();

    if (!result.success) {
      throw new HTTPException(500, { message: 'Error al actualizar orden' });
    }

    return c.json({
      success: true,
      data: { order_id: orderId, ...body }
    });

  } catch (error) {
    console.error('Error:', error);
    if (error instanceof HTTPException) throw error;
    throw new HTTPException(500, { message: 'Error interno del servidor' });
  }
};
