import { Context } from "hono";
import { db } from "../config/db.js";

// 🚀 Obtener todos los productos
export const getProducts = async (c: Context) => {
  try {
    const { results } = await db(c.env).prepare("SELECT * FROM products").all();
    return c.json(results);
  } catch (error) {
    console.error("🚨 Error al obtener productos:", error);
    return c.json({ error: "Error al obtener productos" }, 500);
  }
};


// 🔍 Obtener un solo producto por ID
// 🔍 Obtener un solo producto por ID

export const getProductById = async (c: Context) => {
  const id = c.req.param("id");

  try {
    const stmt = db(c.env)
      .prepare("SELECT * FROM products WHERE id = ?")
      .bind(id); // Usa .bind()

    const { results } = await stmt.all();

    if (!results?.[0]) {
      return c.json({ error: "Producto no encontrado" }, 404);
    }

    // Asegúrate de parsear los campos JSON
    const product = results[0];
    product.image = JSON.parse(product.image);
    product.more_details = JSON.parse(product.more_details);

    return c.json(product);
  } catch (error) {
    console.error("🚨 Error al obtener producto:", error);
    return c.json({ error: "Error interno del servidor" }, 500);
  }
};


// ➕ Añadir un nuevo producto (ADMIN)
export const addProduct = async (c) => {
  try {
    const { name, image, category_id, unit, stock, price, discount, description, more_details, publish } = await c.req.json();
    
    // Validación de datos
    if (!name || !category_id || !price) {
      return c.json({ error: 'Faltan campos obligatorios' }, 400);
    }

    // Crear el producto en la base de datos (por ejemplo, usando D1)
    const result = await c.env.DB.prepare(`
      INSERT INTO products (name, image, category_id, unit, stock, price, discount, description, more_details, publish)
      VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?)
    `).bind(name, JSON.stringify(image), category_id, unit, stock, price, discount, description, JSON.stringify(more_details), publish)
      .run();

    return c.json({ message: 'Producto añadido correctamente' });
  } catch (error) {
    console.error(error);
    return c.json({ error: 'Error al añadir producto' }, 500);
  }
};


// ✏️ Actualizar producto (ADMIN)
export const updateProduct = async (c) => {
  try {
    const { name, image, category_id, unit, stock, price, discount, description, more_details, publish } = await c.req.json();
    const id = c.req.param("id");

    // Validación de datos
    if (!id) {
      return c.json({ error: "ID del producto es requerido" }, 400);
    }
    if (!name || !category_id || !price) {
      return c.json({ error: "Faltan campos obligatorios" }, 400);
    }

    // Ejecutar la actualización en la base de datos
    const result = await c.env.DB.prepare(`
      UPDATE products SET 
        name = ?, 
        image = ?, 
        category_id = ?, 
        unit = ?, 
        stock = ?, 
        price = ?, 
        discount = ?, 
        description = ?, 
        more_details = ?, 
        publish = ?, 
        updated_at = CURRENT_TIMESTAMP
      WHERE id = ?
    `)
      .bind(name, JSON.stringify(image), category_id, unit, stock, price, discount, description, JSON.stringify(more_details), publish, id)
      .run();

    if (result.changes === 0) {
      return c.json({ error: "Producto no encontrado o sin cambios" }, 404);
    }

    return c.json({ message: "Producto actualizado correctamente" });
  } catch (error) {
    console.error("🚨 Error al actualizar producto:", error);
    return c.json({ error: "Error interno al actualizar producto" }, 500);
  }
};





// 🗑️ Eliminar producto (ADMIN)

export const deleteProduct = async (c: Context) => {
  const id = c.req.param("id");

  try {
    // 🔄 Usar .bind(id) antes de .run()
    const result = await db(c.env)
      .prepare("DELETE FROM products WHERE id = ?")
      .bind(id) // Vincular el parámetro
      .run();

    if (!result.success) {
      return c.json({ error: "Error al eliminar producto" }, 500);
    }

    return c.json({ message: "Producto eliminado correctamente" });
  } catch (error) {
    console.error("🚨 Error al eliminar producto:", error);
    return c.json({ error: "Error interno del servidor" }, 500);
  }
};