import { Context } from 'hono';

export const createCategory = async (c: Context) => {
    try {
        const body = await c.req.json();
        const { name, image } = body;

        // Validación
        if (!name?.trim() || !image?.trim()) {
            return c.json({ error: 'Nombre e imagen son requeridos' }, 400);
        }

        // Verificar categoría existente
        const exists = await c.env.DB.prepare(
            'SELECT id FROM categories WHERE name = ?'
        ).bind(name).first();

        if (exists) {
            return c.json({ error: 'La categoría ya existe' }, 409);
        }

        // Insertar en DB
        const { success } = await c.env.DB.prepare(
            'INSERT INTO categories (name, image) VALUES (?, ?)'
        ).bind(name, image).run();

        if (!success) {
            throw new Error('Error en la inserción');
        }

        return c.json({ message: 'Categoría creada', data: { name, image } }, 201);

    } catch (error) {
        console.error('Error:', error);
        return c.json({ error: 'Error interno del servidor' }, 500);
    }
};


