import { SignJWT , jwtVerify  } from 'jose';

// Controlador de registro
export const register = async (c) => {
  try {
    const { email, password, name = "" } = await c.req.json();
    if (!email || !password) return c.json({ error: "Campos requeridos" }, 400);

    const existing = await c.env.DB
      .prepare("SELECT email FROM users WHERE email = ?")
      .bind(email)
      .first();

    if (existing) return c.json({ error: "Email ya registrado" }, 400);

    await c.env.DB
      .prepare("INSERT INTO users (email, password, name) VALUES (?, ?, ?)")
      .bind(email, password, name)
      .run();

    return c.json({ success: true });
  } catch (error) {
    console.error("Error en /register:", error);
    return c.json({ error: "Error del servidor" }, 500);
  }
};

// Controlador de login



export const login = async (c) => {
  try {
    const { email, password } = await c.req.json();
    console.log("📩 Datos recibidos en login:", email, password);

    const user = await c.env.DB
      .prepare("SELECT * FROM users WHERE email = ?")
      .bind(email)
      .first();

    if (!user || user.password !== password) {
      console.log("❌ Credenciales incorrectas");
      return c.json({ error: "Credenciales inválidas" }, 401);
    }

    const secret = new TextEncoder().encode(c.env.JWT_SECRET);
    const token = await new SignJWT({ id: user.id, email: user.email })  
      .setProtectedHeader({ alg: "HS256" })
      .setExpirationTime("2h")
      .sign(secret);

    console.log("✅ Token generado:", token);

    return c.json({ success: true, token, user: { id: user.id, name: user.name, email: user.email } });
  } catch (error) {
    console.error("🚨 Error en /login:", error);
    return c.json({ error: "Error del servidor" }, 500);
  }
};






// Controlador para verificar autenticación
// Controlador para verificar autenticación
export const checkAuth = async (c) => {
  try {
    const authHeader = c.req.header("Authorization");
    if (!authHeader) {
      console.log("❌ No se envió el header Authorization");
      return c.json({ success: false, error: "Acceso no autorizado" }, 401);
    }

    const token = authHeader.split(" ")[1];
    if (!token) {
      console.log("❌ Token no encontrado en el header");
      return c.json({ success: false, error: "Token no proporcionado" }, 401);
    }

    const secret = new TextEncoder().encode(c.env.JWT_SECRET);
    
    try {
      const { payload } = await jwtVerify(token, secret);
      console.log("🔑 Payload del token:", payload);

      const user = await c.env.DB
        .prepare("SELECT id, email, name FROM users WHERE id = ?")
        .bind(payload.id)
        .first();

      console.log("📡 Usuario encontrado en DB:", user);

      if (!user) {
        console.log("❌ Usuario no encontrado en la base de datos.");
        return c.json({ success: false, error: "Usuario no encontrado" }, 404);
      }

      return c.json({ success: true, user });
    } catch (error) {
      console.error("🚨 Error verificando token:", error);
      return c.json({ success: false, error: "Token inválido o expirado" }, 401);
    }
  } catch (error) {
    console.error("🚨 Error en /check:", error);
    return c.json({ success: false, error: "Error del servidor" }, 500);
  }
};





export const getUserDetails = async (c) => {
  try {
    const authHeader = c.req.header("Authorization");
    if (!authHeader) return c.json({ success: false, error: "Acceso no autorizado" }, 401);

    const token = authHeader.split(" ")[1];
    if (!token) return c.json({ success: false, error: "Token no proporcionado" }, 401);

    const secret = new TextEncoder().encode(c.env.JWT_SECRET);
    
    try {
      const { payload } = await jwtVerify(token, secret);
      
      // Obtener información del usuario
      const user = await c.env.DB
      .prepare("SELECT id, email, name, role ,mobile, avatar FROM users WHERE id = ?") // 🔹 Agregar avatar
      .bind(payload.id)
      .first();
    

      if (!user) return c.json({ success: false, error: "Usuario no encontrado" }, 404);

      console.log("📡 Usuario encontrado en DB:", user);

      return c.json({ success: true, user }); // 👈 Asegúrate de devolver success: true
    } catch (error) {
      console.error("🚨 Error de verificación de token:", error);
      return c.json({ success: false, error: "Token inválido o expirado" }, 401);
    }
  } catch (error) {
    console.error("🚨 Error en /get-user-details:", error);
    return c.json({ success: false, error: "Error del servidor" }, 500);
  }
};




export const updateUserProfile = async (c) => {
  try {
    const authHeader = c.req.header("Authorization");
    if (!authHeader) return c.json({ success: false, error: "Acceso no autorizado" }, 401);

    const token = authHeader.split(" ")[1];
    if (!token) return c.json({ success: false, error: "Token no proporcionado" }, 401);

    const secret = new TextEncoder().encode(c.env.JWT_SECRET);

    try {
      const { payload } = await jwtVerify(token, secret);
      const userId = payload.id;

      // Obtener los datos del frontend
      const { name, avatar, role, mobile } = await c.req.json();
      if (!name) return c.json({ success: false, error: "El nombre es obligatorio" }, 400);

      // Validación del teléfono si se proporciona
      if (mobile) {
        const phoneRegex = /^\+?[0-9\s\-\(\)]{7,}$/;
        if (!phoneRegex.test(mobile)) {
          return c.json({ 
            success: false, 
            error: "Formato de teléfono inválido. Ejemplo válido: +34 123 456 789" 
          }, 400);
        }
      }

      // Construcción dinámica de la consulta
      const setClauses = ["name = ?"];
      const params = [name];

      if (avatar) {
        setClauses.push("avatar = ?");
        params.push(avatar);
      }

      if (role) {
        setClauses.push("role = ?");
        params.push(role);
      }

      if (mobile) {
        setClauses.push("mobile = ?");
        params.push(mobile);
      }

      setClauses.push("updated_at = CURRENT_TIMESTAMP");
      params.push(userId); // Para el WHERE id = ?

      const query = `UPDATE users SET ${setClauses.join(", ")} WHERE id = ?`;

      const update = await c.env.DB.prepare(query).bind(...params).run();

      if (update.changes === 0) {
        return c.json({ success: false, error: "No se pudo actualizar el perfil" }, 400);
      }

      console.log(`✅ Perfil actualizado: ${name}${
        avatar ? ", avatar actualizado" : ""
      }${role ? `, rol: ${role}` : ""}${
        mobile ? `, teléfono: ${mobile}` : ""
      }`);
      
      return c.json({ success: true, message: "Perfil actualizado correctamente" });

    } catch (error) {
      console.error("🚨 Error de verificación de token:", error);
      return c.json({ success: false, error: "Token inválido o expirado" }, 401);
    }
  } catch (error) {
    console.error("🚨 Error en /update-profile:", error);
    return c.json({ success: false, error: "Error del servidor" }, 500);
  }
};

