import { Hono } from 'hono';
import { register, login,checkAuth,getUserDetails,updateUserProfile} from '../controllers/auth.controller.ts';

// Tipado para variables de entorno
type Bindings = {
  DB: D1Database;
  JWT_SECRET: string;
};

const auth = new Hono<{ Bindings: Bindings }>();

auth.post('/register', register);

auth.post('/login', login);
auth.post('/check', checkAuth);
auth.get('/get-userDetails', getUserDetails);
auth.put('/update-profile', updateUserProfile);
export default auth;