import { Hono } from 'hono';
import { cors } from 'hono/cors';
import authRoutes from '../routes/auth.routes';
import adminRoutes from '../routes/admin.routes';  // Importar las rutas de admin
import productRouter from "../routes/product.route";
import categoryRouter from '../routes/category.routes.js';
import addressRoutes from '../routes/address.route';
import orderRouter from '../routes/order.route';
const app = new Hono();

// Configuración global
app.use('*', cors({
  origin: ['http://localhost:5173','http://localhost:3000'],
  allowMethods: ['GET', 'POST', 'PUT', 'DELETE'],
  credentials: true
}));

app.route("/api/products", productRouter);
// Rutas públicas
app.route('/api/user', authRoutes);

// Rutas protegidas de administración
app.route('/api/admin', adminRoutes);  // Montar las rutas bajo /api/admin

app.route('/api/categories', categoryRouter);

//route adresses
app.route('/api/addresses', addressRoutes);


//order 
app.route('/api/orders', orderRouter);
export default app;